.recipe {
    max-width: 1200px;
    margin: 40px auto;
    text-align: center;
    background: #fff;
    padding: 40px;
    box-sizing: border-box;
    position: relative;
    border-radius: 8px;
}
.recipe ul {
    display: flex;
    padding: 0;
    justify-content: center;
    margin-top: 5px;
}
.recipe li {
    list-style-type: none;
    margin-right: 10px; 
    color: #777;
}
.recipe li::after {
    content: ",";
}
.recipe li:last-child::after {
    content: ".";
}
.method {
    text-align: left;
    line-height: 1.5em;
    font-family: 'Playpen Sans', cursive;
    text-indent: 40px;
}
.method::first-letter { text-transform: capitalize; }
.recipe-title {
    margin-top: 20px;
    display: flex;
}
.return {
    position: absolute;
    height: 48px;
    width: 48px;
    top: 10px;
    left: 10px;
    cursor: pointer;
    filter: invert(60%);
}

@media screen and (max-width: 670px) {
    .recipe {
        padding: 20px;
    } 
    .recipe ul {
        display: grid;
        
    }
    .recipe li {
        text-align: left;
        list-style-type:square;
    }
    .return {
        height: 32px;
        width: 32px;
    }
}

/* dark mode */
.recipe.dark {
    background-color: #555;
    color: #e4e4e4;
}