.navbar {
    background-color: #58249c;
    padding: 20px;
    color: #fff;
    transition: all .5s ease;
}
.navbar nav {
    display: flex;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
}
.navbar a.brand {
    margin-right: auto;
    color: #fff;
    text-decoration: none;
}
.navbar a.btn {
    color: #fff;
    text-decoration: none;
    margin-left: 10px;
    padding: 8px;
    border: 1px solid #fff;
    border-radius: 4px;
}
.navbar a.btn:hover {
    background-color: #fff;
    color: #333;
}
.navbar button {
    font-size: 1em;
    background-color: transparent;
    color: #fff;
    text-decoration: none;
    margin-left: 10px;
    padding: 8px;
    border: 1px solid #fff;
    border-radius: 4px;
    cursor: pointer;
}
.navbar button:hover {
    background-color: #fff;
    color: #333;
}

@media screen and (max-width: 900px) {
    .navbar .desktop {
        display: none;
    }
    
}