.home {
    max-width: 1200px;
    margin: 20px auto;
}
.home .create-recipe {
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.home .create-recipe a {
    display: flex;
    align-items: center;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 1em;
    text-decoration: none;
    color: #777;
}
.home .create-recipe a img {
    margin-right: 4px;
    transition: .5s ease;
}
.home .create-recipe a:hover img {
    transform: rotateZ(180deg);
}
.home.dark {
    color: #ddd;
}
.home.dark .create-recipe a {
    color: #bbb;
}