.App {
  min-height: 100%;
}
.App.dark {
  background-color: #333;
}

.App .content {
  min-height: calc(100vh - 190px);
  padding: 20px 0;
}

.mobile {
  display: none;
}

@media screen and (max-width: 1220px) {
  .App .content {
      padding: 20px;
  }
}

@media screen and (max-width: 900px) {
  .mobile {
    display: block;
  }
  .dark .mobile {
    color: #e4e4e4;
  }
}

@media screen and (max-width: 670px) {
  .App .content {
    min-height: calc(100vh - 175px);
  }
}