.signup-form {
    color: black;
    max-width: 360px;
    margin: 20px auto;
}
.signup-form .invalid {
    background-color: rgb(255, 117, 117, .3)
}
.signup-form label span {
    display: block;
    margin: 20px 0 10px;
}
.signup-form button {
    display: block;
    font-size: 1em;
    color: #fff;
    padding: 8px 20px;
    border: 0;
    border-radius: 4px;
    background-color: #58249c;
    cursor: pointer;
    text-decoration: none;
    margin: 20px auto;
}
.signup-form button:hover {
    box-shadow: 0 4px 0 #381763;
    margin-top: 16px;
    margin-bottom: 24px;
}
.signup-form .error-message {
    color: red;
}
.signup-form.dark h2 {
    color: #d4d4d4;
}
.signup-form.dark input
 {
    background-color: #555;
    color: #222;
}
.signup-form.dark label span {
    color: #e4e4e4;
}