.create {
    color: #555;
    max-width: 480px;
    margin: 20px auto;
}
.create label span {
    display: block;
    margin: 20px 0 10px;
}
.create p {
    margin-top: 10px;
    font-size: .8em;
}
.create button {
    display: block;
    font-size: 1em;
    color: #fff;
    padding: 8px 20px;
    border: 0;
    border-radius: 4px;
    background-color: #58249c;
    cursor: pointer;
    text-decoration: none;
    margin: 20px auto;
}
.ingredients {
    display: flex;
    align-items: center;
}
.ingredients button {
    margin: 0 0 0 10px;
}

.create.dark label span {
    color: #e4e4e4;
}
.create.dark p{
    color: #a4a4a4;
}
.create.dark input,
.create.dark textarea
 {
    background-color: #555;
    color: #222;
}